import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalComponent } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Observable, Subscription } from 'rxjs';
import {
  ContactList,
  CoverPage,
  CoverPageBodyType,
  CoverPagesDataSource,
  FieldDefinition,
  Individual,
  MetaDataType,
  PrintToService,
  RolePermissions,
  SessionStorageService,
  Strings,
  Substitution,
  recipientDialogState,
} from 'shared-code-lib';
import { HtmlEditorComponent } from '../../../blocks/components/html-editor/html-editor.component';
import { Log } from '../../../blocks/core/log';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { SendFaxService } from '../../../blocks/services/send-fax.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-details-form',
  templateUrl: './details-form.component.html',
  styleUrls: ['./details-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DetailsFormComponent implements OnInit, OnDestroy {
  private editor: HtmlEditorComponent;
  @ViewChild('editor') set content(content: HtmlEditorComponent) {
    if (content) {
      this.editor = content;
      this.formData.hasHtmlValue = content.hasHtmlValue;
    }
  }

  readonly recipientsField = 'recipients';
  readonly sendfaxModel = 'SendFax';

  @Input() formData;
  @Input() validateForm: () => void;
  @Output() filesChanged = new EventEmitter();
  @Output() recipientsChanged = new EventEmitter();
  @Input() substitutions: Array<Substitution>;

  recipient: any = new Individual('', '', '', '');

  supportedFiles: string;
  uploadSubscription: any;
  initialMessage: string;

  contactTypes = [
    { value: 'ContactList', name: 'Contact List' },
    { value: 'Individual', name: 'Individual' },
  ];
  groups = [
    { value: 'Interfax', name: 'Interfax' },
    { value: 'Bamba', name: 'Bamba' },
  ];

  individualRecipientModalInstance: NgxSmartModalComponent;
  interfaxContactListModal: NgxSmartModalComponent;

  showIndividualRecipientModalBody = false;

  dialogState = recipientDialogState.New;
  selectedContactLists: Array<ContactList> = [];
  supportContactLists = false;
  contactLists: Array<ContactList> = [];

  showContactListModal = false;
  editorPlaceHolderText: string;
  fieldMessage: FieldDefinition;

  individualRecipientModalTitle: string;

  fieldCoverPage: FieldDefinition;
  defaultCoverPage: CoverPage = null;

  isFaxMobile = false;

  refocusFaxRecipients$: Observable<void>;

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private userStateService: UserStateService,
    public spinnerStateService: SpinnerStateService,
    public interFAXService: InterFAXService,
    private sessionStorageService: SessionStorageService,
    private toastr: ToastrService,
    private printToService: PrintToService,
    private sendFaxService: SendFaxService
  ) {}

  ngOnInit(): void {
    this.refocusFaxRecipients$ = this.sendFaxService.sendFormDiscarded$;

    if (this.printToService.isPrintToMode && !this.printToService.isInitialized) this.printToService.initialize();

    this.isFaxMobile = this.sessionStorageService.getIsInterFaxMobile();
    this.subscriptions.add(
      this.sessionStorageService.onIsInterFaxMobileChanged.subscribe((x) => (this.isFaxMobile = x))
    );

    this.supportedFiles = this.userStateService.userProfile.fax.fileTypes.join(', ').toUpperCase();

    this.editorPlaceHolderText = this.translate.instant('SendFax-Details-Form.AnyContentEnteredSendAsPage');

    this.supportContactLists =
      !this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PCI) &&
      this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PERMISSIONS_ALLOWCONTACTLISTS);

    if (this.supportContactLists) {
      this.subscriptions.add(
        this.interFAXService.getContactLists(
          (cls) => (this.contactLists = cls),
          (err) => (this.contactLists = [])
        )
      );
    }

    this.initialMessage = '';

    if (this.formData) {
      this.initialMessage = this.formData.message;

      this.subscriptions.add(
        this.formData.onFormClear.subscribe(() => {
          this.resetHtmlEditor();

          if (this.defaultCoverPage) {
            this.formData.coverPageId = this.defaultCoverPage.id;
            this.onCoverPageLoaded(this.defaultCoverPage);
          } else {
            this.clearSelectedCoverPage();
          }
        })
      );

      if (Strings.notEmpty(this.formData.chipText)) {
        this.formData.recipients.push({
          display: this.formData.chipText,
          value: new Individual('', this.formData.chipText, '', ''),
        });
        this.formData.chipText = '';
      }
    }

    this.createFields();
    this.loadDefaultCoverPage();
  }

  createFields = (): void => {
    this.fieldCoverPage = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('SendFax-Details-Form.Cover-Page'),
      placeholder: this.translate.instant('SendFax-Details-Form.Cover-Page'),
      datasource: new CoverPagesDataSource(
        this.interFAXService,
        this.translate,
        this.userStateService.interFAXUsername
      ),
      defaultValue: '0',
    });

    this.fieldMessage = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: this.translate.instant('SendFax-Details-Form.Message'),
      placeholder: this.translate.instant('SendFax-Details-Form.Message'),
      autosizeMinRows: 10,
      scrollHeight: '192px',
    });
  };

  loadDefaultCoverPage(): void {
    const defaultCoverPageSettings = this.userStateService.userProfile.fax.advancePageSetting.find(
      (x) => x.name == 'CoverPageId'
    );

    let defaultCoverPageId = defaultCoverPageSettings?.value
      ? parseInt(defaultCoverPageSettings.value).toString()
      : '0';

    if (!this.isCoverPageIdValid(defaultCoverPageId)) return;

    this.interFAXService.getUserCoverPage(
      +defaultCoverPageId,
      (coverPage: CoverPage) => {
        this.defaultCoverPage = coverPage;
        this.onCoverPageLoaded(coverPage);
      },
      () => {
        this.defaultCoverPage = null;
        this.clearSelectedCoverPage();
      }
    );
  }

  resetHtmlEditor = (): void => {
    this.editor.setValue('');
    this.initialMessage = '';
    this.editor.setFontType(this.editor.getDefaultFontType(), false);
    this.editor.setFontSize(this.editor.getDefaultFontSizePx(), false);
  };

  textEditorChange = (value: string): void => {
    this.formData.message = value;
    this.validateForm();
  };

  plainTextEditorChanged = (value: any): void => {
    this.formData.message = value;
    this.validateForm();
  };

  onFilesChange(): void {
    this.filesChanged.emit();
  }

  onRecipientsChanged = (recipients: any[]): void => {
    this.formData.recipients = recipients;
    this.recipientsChanged.emit();
  };

  onCoverPageChanged = (value: string): void => {
    if (!this.isCoverPageIdValid(value)) {
      this.initialMessage = this.formData.message;
      this.clearSelectedCoverPage();
      return;
    }

    this.interFAXService.getUserCoverPage(+value, this.onCoverPageLoaded, this.onCoverPageLoadFailed);
  };

  isCoverPageIdValid(value: string): boolean {
    const parsedValue = Number.parseInt(value, 10);
    return !isNaN(parsedValue) && parsedValue > 0;
  }

  onCoverPageLoaded = (coverPage: CoverPage): void => {
    this.formData.coverPageId = coverPage.id;
    this.formData.coverPage = coverPage;
    this.retainUserInput();
    this.validateForm();
  };

  onCoverPageLoadFailed = (err: any): void => {
    this.clearSelectedCoverPage();

    if (err.status == 404) return;

    Log.error('cover-page.load-cover-page-failed', err);
    this.toastr.error(this.translate.instant('cover-page.load-cover-page-failed'));
  };

  clearSelectedCoverPage(): void {
    this.formData.coverPage = null;
    this.formData.coverPageId = '0';
    this.validateForm();
  }

  retainUserInput = (): void => {
    if (this.formData.message.length) {
      if (this.formData.coverPage.coverpageBodyType == CoverPageBodyType.Text)
        this.formData.message = Strings.HtmlToText(this.formData.message);

      if (this.formData.coverPage.coverpageBodyType == CoverPageBodyType.Html)
        this.initialMessage = this.formData.message;

      if (this.formData.coverPage.coverpageBodyType == CoverPageBodyType.None)
        this.initialMessage = this.editor.value = this.formData.message = '';
    }
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
