import { Component, OnInit, Input, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { UserStateService } from '../../../blocks/services/user-state.service';
import {
  FieldDefinition,
  IFieldOptions,
  MetaDataType,
  FixedDataSource,
  MetaDataValidity,
  DateTime,
  FaxPageSettings,
  NumberOfAttampts,
  PageOrientation,
  PageSize,
  RenderingOptimization,
  Resolution,
  RolePermissions,
} from 'shared-code-lib';
import { Email } from '../../../blocks/core/email';

@Component({
  selector: 'app-page-attributes-form',
  templateUrl: './page-attributes-form.component.html',
  styleUrls: ['./page-attributes-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PageAttributesFormComponent implements OnInit {
  _faxPageSettings: FaxPageSettings;

  @Input() set faxPageSettings(value) {
    this._faxPageSettings = value;
    this.initializeSendAfter();
    this.validate();
    this.faxPageSettingsChanged.emit(this.faxPageSettings);
  }

  get faxPageSettings(): FaxPageSettings {
    return this._faxPageSettings;
  }

  @Input() showSendAfterContactListsWarning: boolean = true;
  @Output() faxPageSettingsChanged = new EventEmitter<FaxPageSettings>();

  saveAsDefault = false;

  sendAfterDay = '';
  sendAfterHour = '';
  sendAfterMinute = '';

  fieldFaxCSID: FieldDefinition;
  fieldNumberofAttempts: FieldDefinition;
  fieldPageOrientation: FieldDefinition;
  fieldPageSize: FieldDefinition;
  fieldRenderingOptimization: FieldDefinition;
  fieldResolution: FieldDefinition;
  fieldSendAfterDay: FieldDefinition;
  fieldSendAfterHour: FieldDefinition;
  fieldSendAfterMinute: FieldDefinition;
  fieldFeedbackEmail: FieldDefinition;
  fieldFeedbackEmailValidity: MetaDataValidity;
  showSendAfter: boolean;

  constructor(private translate: TranslateService, private userState: UserStateService) { }

  ngOnInit() {
    this.showSendAfter = this.userState.hasRolePermission(RolePermissions.INTERFAX_CAN_USE_SENDAFTER);

    this.createFields();
    this.initializeSendAfter();
  }

  createFields() {
    this.fieldFaxCSID = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('SendFax-Attributes-Form.FaxCSID'),
      placeholder: this.translate.instant('SendFax-Attributes-Form.FaxCSID-Placeholder'),
      required: false,
    });

    this.fieldNumberofAttempts = this.createListField(NumberOfAttampts, 'NumberOfAttempts');

    this.fieldPageOrientation = this.createListField(PageOrientation, 'PageOrientation', true);

    this.fieldPageSize = this.createListField(PageSize, 'PageSize', true);

    this.fieldRenderingOptimization = this.createListField(RenderingOptimization, 'RenderingOptimization', true);

    this.fieldResolution = this.createListField(Resolution, 'Resolution', true);

    this.fieldFeedbackEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('SendFax-Attributes-Form.FeedbackEmail'),
      placeholder: '',
      required: false,
    });

    const sendAfterDayOptions = new Array<IFieldOptions>();
    for (var day = 0; day < 30; day++) {
      const date = new Date();
      date.setDate(date.getDate() + day);

      sendAfterDayOptions.push({ name: this.getSendAfterDayText(day, date), value: this.pad(day) });
    }

    const sendAfterHourOptions = new Array<IFieldOptions>();
    for (var hour = 0; hour < 24; hour++) {
      sendAfterHourOptions.push({ name: this.pad(hour), value: hour.toString() });
    }

    const sendAfterMinuteOptions = new Array<IFieldOptions>();
    for (var minute = 0; minute < 60; minute++) {
      sendAfterMinuteOptions.push({ name: this.pad(minute), value: minute.toString() });
    }

    this.fieldSendAfterDay = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('SendFax-Attributes-Form.send-after-day'),
      placeholder: '',
      required: true,
      datasource: new FixedDataSource(sendAfterDayOptions),
    });

    this.fieldSendAfterHour = new FieldDefinition({
      type: MetaDataType.List,
      label: 'hour',
      placeholder: '',
      required: true,
      hideLabel: true,
      datasource: new FixedDataSource(sendAfterHourOptions),
    });

    this.fieldSendAfterMinute = new FieldDefinition({
      type: MetaDataType.List,
      label: 'minute',
      placeholder: '',
      required: true,
      hideLabel: true,
      datasource: new FixedDataSource(sendAfterMinuteOptions),
    });

    const { feedbackEmail } = this.faxPageSettings;
    if (!feedbackEmail || Email.isValid(feedbackEmail)) {
      this.fieldFeedbackEmailValidity = null;
    } else {
      this.fieldFeedbackEmailValidity = MetaDataValidity.notValid;
    }
  }

  getSendAfterDayText = (day: number, date: Date): string => {
    if (day == 0) return this.translate.instant('SendFax-Attributes-Form.send-after-today');
    if (day == 1) return this.translate.instant('SendFax-Attributes-Form.send-after-tomorrow');

    return date.toLocaleDateString();
  };

  pad = (n: number): string => {
    if (n > 9) return n.toString();
    return '0' + n.toString();
  };

  createListField(specificationName: string, labelText: string, sort: boolean = false): FieldDefinition {
    return new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant(`SendFax-Attributes-Form.${labelText}`),
      datasource: new FixedDataSource(this.getSettingOptions(specificationName), sort),
      required: true,
    });
  }

  canShowNotificationEmail = (): boolean => {
    return !this.userState.userProfile.useDefaultReplyAddress;
  };

  getSettingOptions(specificationItemName: string): IFieldOptions[] {
    const options = new Array<IFieldOptions>();

    const specificationItem = this.userState.userProfile.fax.advancePageSetting.find(
      (item) => item.name === specificationItemName
    );
    if (!specificationItem) return options;

    specificationItem['optionalValues'].forEach((element) => {
      options.push({ name: this.getResourcedOptionName(element), value: element });
    });

    const defaultValue = specificationItem['value'];

    if (!options.some((option) => option.value == defaultValue)) {
      options.push({ name: this.getResourcedOptionName(defaultValue), value: defaultValue });
    }

    return options;
  }

  getResourcedOptionName(optionName): string {
    if (Number(optionName)) return optionName;
    const translationKey = `SendFax-Attributes-Form.Option-Value-${optionName}`;
    const translation = this.translate.instant(translationKey);
    if (translation === translationKey) return optionName;
    return translation;
  }

  initializeSendAfter = (): void => {
    let day = 0;

    if (
      this.faxPageSettings &&
      this.faxPageSettings.sendAfterDateTime &&
      this.faxPageSettings.sendAfterDateTime > new Date()
    ) {
      day = DateTime.daysBetween(this.faxPageSettings.sendAfterDateTime, new Date());
      this.sendAfterDay = this.pad(day);
      this.sendAfterHour = this.faxPageSettings.sendAfterDateTime.getHours().toString();
      this.sendAfterMinute = this.faxPageSettings.sendAfterDateTime.getMinutes().toString();
      return;
    }

    const date = new Date();
    if (date.getHours() > 17) day = 1;

    this.sendAfterDay = this.pad(day);
    this.sendAfterHour = '17';
    this.sendAfterMinute = '0';
  };

  onSendAfter = (b: boolean): void => {
    this.faxPageSettings.sendAfter = b;
    this.onSettingsChanged();
  };

  validate = (): void => {
    this.fieldFeedbackEmailValidity =
      !this.faxPageSettings.feedbackEmail || Email.isValid(this.faxPageSettings.feedbackEmail)
        ? null
        : MetaDataValidity.notValid;
  };

  onFieldEmailChange(value: string): void {
    this.validate();
    this.onSettingsChanged();
  }

  onSettingsChanged() {
    const days = parseInt(this.sendAfterDay);

    const date = new Date();
    date.setDate(date.getDate() + days);

    const year = date.getFullYear();
    const month = date.getMonth();
    const day = date.getDate();
    const hour = parseInt(this.sendAfterHour);
    const minute = parseInt(this.sendAfterMinute);

    this.faxPageSettings.sendAfterDateTime = new Date(year, month, day, hour, minute);
    this.faxPageSettingsChanged.emit(this.faxPageSettings);
  }
}
