import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { Subscription } from 'rxjs';
import {
  CoverPageBodyType,
  CoverPageType,
  DetailsView,
  FaxForm,
  FaxPageSettings,
  PageAttributesView,
  PrintToService,
  Recipient,
  SendFaxRecipientType,
  Substitution,
  Substitutions,
  Tab,
} from 'shared-code-lib';
import { PageAttributesFormComponent } from '../../../../src/app/views/send-fax/page-attributes-form/page-attributes-form.component';
import { ErrorsService } from '../../blocks/services/errors.service';
import { Control } from '../../blocks/services/models';
import { SendFaxService } from '../../blocks/services/send-fax.service';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';

@Component({
  selector: 'app-send-fax',
  templateUrl: './send-fax.component.html',
  styleUrls: ['./send-fax.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SendFaxComponent implements OnInit, OnDestroy, AfterViewInit {
  PageAttributesView = PageAttributesView;
  DetailsView = DetailsView;
  constructor(
    private translateService: TranslateService,
    private userState: UserStateService,
    private errorsService: ErrorsService,
    private spinnerState: SpinnerStateService,
    private printToService: PrintToService,
    public ngxSmartModalService: NgxSmartModalService,
    private cdr: ChangeDetectorRef,
    private sendFaxService: SendFaxService
  ) {}
  @ViewChild(PageAttributesFormComponent) optionsTabPage: PageAttributesFormComponent;
  @ViewChild('scrollbar', { static: true })
  scrollbar?: PerfectScrollbarDirective;
  activeTab = DetailsView;
  tabs = new Array<Tab>();

  detailForm = new FaxForm({});
  previewDetailForm: FaxForm;
  faxUserProfileLoaded = false;
  saveButton: Control;
  discardButton: Control;
  previewButton: Control;
  separator: Control;
  rightControls: Array<Control> = [];
  substitutions: Array<Substitution> = [];

  subscription = new Subscription();

  ngOnInit() {
    this.createTabs();
    this.createRightButtons();
    this.substitutions = new Substitutions(this.userState).getSendFaxSubstitutions();

    this.subscription.add(this.userState.userProfileChanged.subscribe(this.sendFaxService.userProfileChangedHandler));
    this.subscription.add(
      this.sendFaxService.faxUserProfileLoaded$.subscribe((loaded) => (this.faxUserProfileLoaded = loaded))
    );
    this.subscription.add(this.sendFaxService.detailForm$.subscribe((detailForm) => (this.detailForm = detailForm)));
  }

  ngAfterViewInit() {
    this.sendFaxService.loadSendFaxProfile(
      document.querySelector('.sendfax-form'),
      this.onSuccessLoadingProfile,
      this.onFailToLoadProfile
    );
  }

  onFailToLoadProfile = (): void => {
    this.spinnerState.hide();
    this.errorsService.showErrorsModal(['SendFax.InterfaxAuthorizationProblem'], 'SendFax.HeadText');
  };

  onSuccessLoadingProfile = (): void => {
    this.spinnerState.hide();
  };

  createRightButtons = (): void => {
    this.saveButton = this.sendFaxService.createSendFaxControl(
      '.sendfax-form',
      this.invalidFeedbackEmailHandler,
      this.optionsTabPageHandler,
      this.updateScrollbarHandler
    );

    this.discardButton = this.sendFaxService.createDiscardControl(
      this.optionsTabPageHandler,
      this.updateScrollbarHandler
    );

    this.separator = new Control(() => {}, '', '', '', '', 'vertical-separator', false);
    this.separator.isVisible = false;

    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.discardButton);
    this.rightControls.push(this.separator);
    if (!this.printToService.isPrintToMode) {
      this.previewButton = new Control(
        this.preview,
        'Preview',
        'btn btn-light',
        '',
        'Preview',
        'btn',
        false,
        false,
        null,
        false
      );
      this.rightControls.push(this.previewButton);

      this.previewButton.setTranslation(this.translateService.instant('Common.Preview'));
    }
  };

  invalidFeedbackEmailHandler = () => this.activateTab(PageAttributesView);

  optionsTabPageHandler = () => {
    if (!this.optionsTabPage) return;

    this.optionsTabPage.initializeSendAfter();
    this.validateDetailForm();
  };

  updateScrollbarHandler = () => this.scrollbar.update();

  createTabs = (): void => {
    this.tabs.push(new Tab(DetailsView, this.translateService.instant('SendFax.Details')));
    this.tabs.push(new Tab(PageAttributesView, this.translateService.instant('SendFax.Options')));
  };

  activateTab = (view) => {
    if (document.activeElement) (document.activeElement as any).blur();

    this.activeTab = view;
    this.scrollbar.update();
    this.cdr.detectChanges();
  };

  updateFaxPageSettings(settings: FaxPageSettings) {
    this.detailForm.faxPageSettings = settings;
    this.sendFaxService.setDetailForm(this.detailForm);
  }

  validateDetailForm = () => {
    const isValid = this.sendFaxService.isValidDetailForm();
    this.saveButton.disableControl(!isValid);

    if (!this.previewButton) return;
    this.separator.isVisible = this.previewButton.isVisible = this.canPreview();
    this.previewButton.isDisabled = !this.detailForm.recipients || this.detailForm.recipients.length == 0;
  };

  canPreview = (): boolean => {
    if (this.substitutions.find((x) => x.hasSubstitutionMarkers(this.detailForm.message)) != null) return true;
    if (
      this.detailForm?.coverPage != undefined &&
      this.substitutions.find((x) => x.hasSubstitutionMarkers(this.detailForm?.coverPage?.htmlBody)) != null
    )
      return true;

    if (this.detailForm.coverPageId <= 0) return false;
    if (this.detailForm?.coverPage?.type == undefined) return false;
    return !(
      this.detailForm?.coverPage?.type == CoverPageType.Html &&
      this.detailForm?.coverPage?.coverpageBodyType == CoverPageBodyType.None
    );
  };

  preview = (): void => {
    this.previewDetailForm = new FaxForm(this.detailForm);
    this.ngxSmartModalService.getModal('faxPreviewModal').open();
  };

  closePreviewModal = (): void => {
    this.ngxSmartModalService.getModal('faxPreviewModal').close();
    this.previewDetailForm = null;
  };

  onFilesChange(): void {
    this.scrollbar.update();
  }

  onRecipientChanged(): void {
    if (this.previewDetailForm) this.previewDetailForm = new FaxForm(this.detailForm);
  }

  showSendAfterContactListsWarning = (): boolean => {
    return this.detailForm.recipients
      .filter((r) => (r as any).value instanceof Recipient)
      .map((r) => (r as any).value as Recipient)
      .some((r) => r.type == SendFaxRecipientType.ContactList);
  };

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.sendFaxService.resetSendFaxState();
  }
}
