import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import {
  Contact,
  ContactList,
  ContactListRecipient,
  FaxForm,
  FieldDefinition,
  IContact,
  IFaxPreview,
  Key,
  MetaDataType,
  Pagination,
  Recipient,
  Substitution,
} from 'shared-code-lib';
import { Log } from '../../../blocks/core/log';
import { ContentPostService } from '../../../blocks/services/content-post.service';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';

declare const $: any;
@Component({
  selector: 'app-fax-preview',
  templateUrl: './fax-preview.component.html',
  styleUrls: ['./fax-preview.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FaxPreviewComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() faxForm: FaxForm;
  @Input() substitutions: Array<Substitution>;
  @Output() onClose = new EventEmitter();

  @ViewChild('contactInfo', { static: true }) contactInfo: ElementRef;
  @ViewChild('scrollbar', { static: true })
  scrollbar?: PerfectScrollbarDirective;

  recipients: Contact[] = [];
  fieldSubmitSample: FieldDefinition;
  sampleFaxNumber: string;
  errors = [];
  currentPage: number = -1;
  pageSize: number = 1;
  dialogHeight: number = 300;
  currentContact: Contact;
  contactHeadersWidth: number = 100;
  faxPreview: IFaxPreview = {
    prefixClassName: '',
    previewHTML: '',
  };

  @HostListener('document:keyup', ['$event'])
  handleKeyUp(event: KeyboardEvent) {
    if (event.key === Key.Escape) this.close();
  }

  constructor(
    private interfaxService: InterFAXService,
    private toastr: ToastrService,
    private translate: TranslateService,
    private contentPost: ContentPostService,
    private spinner: SpinnerStateService,
    private errorService: ErrorsService
  ) {}

  ngOnInit() {
    this.dialogHeight = document.body.clientHeight - 450;
    this.fieldSubmitSample = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: this.translate.instant('SendFax-Details-Form.EnterSampleFaxNumber'),
      required: false,
    });
    this.reset();

    this.loadPage(this.currentPage);
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.prepareRecipients();
    this.reset();

    this.loadPage(this.currentPage);
  }

  ngAfterViewInit(): void {
    this.calculateContactHeadersWidth();
  }

  reset = (): void => {
    this.currentPage = 1;
    this.sampleFaxNumber = '';
    this.faxPreview = {
      prefixClassName: '',
      previewHTML: '',
    };
    this.currentContact = null;
    this.errors = [];
  };

  prepareRecipients = (): void => {
    this.recipients = [];
    if (!this.faxForm || !this.faxForm.recipients) return;

    this.faxForm.recipients.forEach((x) => {
      if ((x as any).value instanceof ContactListRecipient) {
        this.spinner.show(this.translate.instant('Common.Loading'), document.body);
        this.interfaxService.getContactList(
          ((x as any).value as ContactListRecipient).contactListId,
          this.onContactListLoaded,
          this.onContactListFailed
        );
      } else if (x instanceof Recipient) {
        this.recipients.push(
          new Contact({
            name: (x as any).name,
            faxNumber: (x as any).faxnumber,
            company: (x as any).company,
            phoneNumber: (x as any).phoneNumber,
          } as IContact)
        );
      } else if ((x as any).value instanceof Recipient) {
        this.recipients.push(
          new Contact({
            name: (x as any).value.name,
            faxNumber: (x as any).value.faxnumber,
            company: (x as any).value.company,
            phoneNumber: (x as any).value.phoneNumber,
          } as IContact)
        );
      }
    });
  };

  onContactListLoaded = (list: ContactList): void => {
    list.members.forEach((x) => this.recipients.push(x));
    this.spinner.hide('Common.Loading');

    if (this.currentContact == null) this.loadPage(this.currentPage);
  };

  onContactListFailed = (err): void => {
    Log.error('FaxPreviewComponent.onContactListFailed', err);
    this.toastr.error(this.translate.instant('SendFax-Details-Form.ContactListLoadFailed'));
    this.spinner.hide();
  };

  getCurrentContact = (): Contact => {
    if (
      !this.recipients ||
      this.recipients.length == 0 ||
      this.currentPage < 1 ||
      this.currentPage > this.recipients.length
    )
      return null;

    return this.recipients[this.currentPage - 1];
  };

  calculateContactHeadersWidth = (): void => {
    this.contactHeadersWidth = 20;
    const headers = this.contactInfo.nativeElement.getElementsByClassName('contact-header');
    for (let i = 0; i < headers.length; i++) {
      this.contactHeadersWidth = Math.max(this.contactHeadersWidth, $(headers[i]).width() + 30);
    }
  };

  onSampleFaxNumberChanged = (value): void => {
    this.sampleFaxNumber = value;
  };

  onLoadPage = (pagination: Pagination): void => {
    this.loadPage(pagination.pageNumber);
  };

  loadPage = (page: number): void => {
    this.currentPage = page;
    this.faxPreview = {
      prefixClassName: '',
      previewHTML: '',
    };
    this.currentContact = this.getCurrentContact();
    if (!this.currentContact) return;

    this.calculateContactHeadersWidth();
    this.scrollbar.scrollToTop();

    this.spinner.show(this.translate.instant('SendFax-Details-Form.prepare-preview'), document.documentElement);

    this.contentPost.GetFaxPreview(
      this.getFaxFormForCurrentRecipient(
        this.currentContact.name,
        this.currentContact.faxNumber,
        this.currentContact.company,
        this.currentContact.phoneNumber
      ),
      this.previewLoaded,
      this.previewFailed
    );
  };

  previewLoaded = (result: IFaxPreview): void => {
    this.faxPreview = result;
    this.spinner.hide();
  };

  previewFailed = (err: any): void => {
    this.spinner.hide();
    Log.error('FaxPreviewComponent.previewFailed', err);
    this.errorService.showErrorsModal('SendFax-Details-Form.fax-preview-failed', 'SendFax-Details-Form.fax-preview');
    this.close();
  };

  getFaxFormForCurrentRecipient = (name: string, faxNumber: string, company: string, phoneNumber: string): FaxForm => {
    const sampleDetailForm = new FaxForm(this.faxForm);
    sampleDetailForm.message = sampleDetailForm.message.replace(/Person/g, '%%Fax.Name%%');
    sampleDetailForm.message = sampleDetailForm.message.replace(/Fax/g, '%%Fax.FaxNumber%%');
    sampleDetailForm.message = sampleDetailForm.message.replace(/Company/g, '%%Fax.Company%%');
    sampleDetailForm.message = sampleDetailForm.message.replace(/Voice/g, '%%Fax.PhoneNumber%%');
    (sampleDetailForm.recipients as any) = [
      {
        display: faxNumber,
        value: { id: 1, name: name, faxNumber: faxNumber, company: company, phoneNumber: phoneNumber },
      },
    ];

    return sampleDetailForm;
  };

  sendSampleFax = (): void => {
    this.errors = [];
    this.contentPost.sendFax(
      document.documentElement,
      this.getFaxFormForCurrentRecipient(
        this.currentContact.name,
        this.sampleFaxNumber,
        this.currentContact.company,
        this.currentContact.phoneNumber
      ),
      this.onSampleFaxSent,
      this.onSampleFaxFailed
    );
  };

  onSampleFaxSent = (result: any): void => {
    this.spinner.hide();

    if (result.succeeded) {
      this.toastr.success(this.translate.instant('SendFax-Details-Form.SampleFaxSent'));
      this.close();
      return;
    }
    if (result.errors) {
      result.errors.forEach((error) => {
        this.errors.push(error);
      });
    } else {
      this.onSampleFaxFailed();
    }
  };

  onSampleFaxFailed = (err?: any): void => {
    Log.error('FaxPreviewComponent.onSampleFaxFailed', err);
    this.errors.push(this.translate.instant('SendFax-Details-Form.SampleFaxFailed'));
  };

  close = (): void => {
    this.onClose.emit();
  };
}
