import { Component, OnInit } from '@angular/core';
import { NgxSmartModalService } from 'ngx-smart-modal';

import { PrintToService, SessionStorageService } from 'shared-code-lib';

@Component({
  selector: 'app-fax-success-modal',
  templateUrl: './fax-success-modal.component.html',
  styleUrls: ['./fax-success-modal.component.scss'],
})
export class FaxSuccessModalComponent implements OnInit {
  constructor(
    private printToService: PrintToService,
    public ngxSmartModalService: NgxSmartModalService,
    private sessionStorageService: SessionStorageService
  ) {}

  isFaxMobile = false;

  ngOnInit() {
    this.isFaxMobile = this.sessionStorageService.getIsInterFaxMobile();
  }

  closeSuccessModal = () => {
    this.printToService.closeAppIfPrintTo();
    this.ngxSmartModalService.getModal('sendFaxSuccessModal').close();
  };
}
